.page {
  font-family: "Playfair Display", serif, "Raleway", sans-serif;

  scroll-behavior: smooth;

  * {
    box-sizing: border-box;
  }

  &__body {
    margin: 0;

    background-color: $c-white;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }
}
